
body{
  margin : 0px;
  box-sizing: border-box;
  font-size-adjust: 0.1;
}
#app{
  font-family: 'Roboto', sans-serif;
  background-image: url('./assets/preview.jpg');
  background-size: cover;
  background-position: center center;
}
*:focus {
 outline: none;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .25s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
  opacity: 0
}
.infinite-loading-container [class^=loading-][data-v-50793f02] {
  display: inline-block;
  margin: 10px 0;
  width: 70px;
  height: 70px;
  font-size: 28px;
  line-height: 28px;
  border-radius: 50%;
}
.infinite-status-prompt[data-v-50793f02] {
  color: white;
  font-size: 35px;
  text-align: center;
  padding: 10px 0;
}



