
body{
		box-sizing: border-box;
}
#view-main{
		/*height: 100vh;*/
		box-sizing: border-box;
}
.header{
		width: inherit;
		position: relative;
		-webkit-text-size-adjust : 35%;
}
.brZero{
		border-radius: 0px;
		border : 3px solid #009688;
}
.fontZero {
		font-size: 0px !important;
}
.fontFifty {
		font-size: 50px !important;
}
.fa-times {
		font-size: 0px;
}
.tabbed-nav{
		width: 100%;
		float: left;
		text-align: left;
}
.tabbed-nav span{
		position: absolute;
		right: 0px;
		top: 0px;
		width: 0px;
		transition: all 0.2s ease;
		box-sizing: border-box;
}
.tabbed-nav span input{
		height: 58px;
    width: 100%;
    border: none;
    padding-left: 60px;
    padding-right: 50px;
    box-sizing: border-box;
}
.tabbed-nav span input,
	input::-webkit-input-placeholder {
    font-size: 20px;
    vertical-align: baseline;
}
.back-button {
		position: absolute;
		top: 5px;
		padding: 25px 15px;
		background-position: 50%;
		width: 32px;
		background-image: url('../assets/back-icon.png');
		background-size: 20px;
		background-repeat: no-repeat;
		opacity: 1;
}
.tabbed-nav span .fa-search {
		padding: 15px;
}
.share-button,
	.tabbed-nav span .fa-search {
		position: absolute;
    right: 0px;
    top: 5px;
    font-size: 20px;
    color: white;
}
.share-button {
		right: 50px;
		top: 15px;
		padding: 15px 5px;
    background-image: url('../assets/share-big.png');
		background-position: 50%;
		width: 32px;
		background-size: 25px;
		background-repeat: no-repeat;
}
.tabbed-nav span .fa-times{
		position: absolute;
    right: 20px;
    top: 16.5px;
    font-size: 24px;
    color: darkslategrey;
}
.app-hidden{
		display: none;
}
.tabbed-nav span .fa-arrow-left{
		position: absolute;
		top : 0px;
		left:  0px;
		font-size: 25px;
		color: grey;
		padding: 16.5px;
		opacity: 0;
		transition: opacity 0.3s;
}
.tabbed-nav p {
		font-size: 16px;
    color: white;
    text-align: center;
    padding-bottom: 20px;
    font-weight: bold;
    letter-spacing: 1px;
    margin-top: 0px;
    padding-top: 20px;
    transition: border-bottom 1s;
    margin-bottom: 0px;
    padding-left: 10px;
    box-shadow: 0 5px 5px 1px rgba(0,0,0,0.5);
    box-sizing: border-box;
}
.fixed-part{
		position: fixed;
		top: 0;
		width: 100%;
		background-color: #1976D2;
		z-index: 3;
}
.fade-enter-active, {
	  transition: opacity .25s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
	  opacity: 0
}
.circle {
    position: relative;
    display: block;
    height: 24px;
    width: 24px;
    background-color: white;
    border-radius: 70px;
    z-index: 0;
    border: 10px solid #009688;
    transition: all 0.6s cubic-bezier(0.25, 1.7, 0.35, 0.8);
    z-index: 1;
}
.handle {
		position: absolute;
		background-color: #009688;
		top: 45px;
		left: 10px;
		width: 55px;
		height: 10px;
		display: block;
		-webkit-transform: rotate(45deg);
		        transform: rotate(45deg);
		float: right;
		border-radius: 2px;
}
.handle:after {
		content: "";
    background-color: rgb(0, 150, 136);
    width: 55px;
    height: 10px;
    display: block;
    float: right;
    z-index: -1;
    border-radius: 2px;
    transition: all 0.6s cubic-bezier(0.25, 1.7, 0.35, 0.8);
}
.open .circle {
	  -webkit-transform: scale(0.02);
    transform: scale(0.02);
	  -webkit-transform-origin: 30% 40%;
    transform-origin: 30% 40%;
	  background: white;
	  border-color: white;
}
.open .handle:after {
	  -webkit-transform-origin: center center;
	  transform-origin: center center;
	  -webkit-transform: rotate(90deg);
	  transform: rotate(90deg);
}



