
#listview{
	padding-top: 60px;
	font-family: 'Roboto Condensed', sans-serif;
	height: 100vh;
	overflow-y: scroll;
	overflow-x: hidden;
	font-size-adjust: 0.19;
}
.list{
	background: white;
	padding: 10px;
	margin: 10px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	box-shadow: 0 5px 10px rgba(0,0,0,0.19), 0 3px 3px rgba(0,0,0,0.23);
}
.list-left{
	width: 80%;
}
.list-right{
	width: 20%;
	position: relative;
	text-align: center;
}
.distanceInfo{
	position: absolute;
    top: 5px;
    right: 5px;
}
.list-right i {
	font-size: 12px;
	margin-right: 5px;
}
.list-right span{
	font-size: 10px;
}
.list-right button {
	position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto auto;
    font-size: 12px;
    border: 0px;
    height: 30px;
    width: 18vw;
    padding: 0px 5px;
    color: white;
    letter-spacing: 1px;
    background-color: #1976D2;
    border-radius: 50px;
}
.list-right button:focus {outline:0;
}
.list-favicon-job{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	color : #babcbe;
}
.list-favicon-job p {
	margin: 0px;
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
	padding: 5px;
	font-size: 13px;
	color: darkslategrey;
}
.list-favicon-job i{
	padding: 5px;
	width: 5%;
	font-size: 13px;
}
.infinite-status-prompt{
	font-size: 15px !important;
	color: white !important;
}
.infinite-loading-container [class^=loading-][data-v-50793f02] {
    display: inline-block;
    margin: 15px 0;
    width: 25px !important;
    height: 25px !important;
    font-size: 18px;
    line-height: 28px;
    border-radius: 50%;
}
.loading-default{
	border : 1px solid white !important;
}
.infinite-loading-container{
	padding: 5px !important;
}




